<template><section><html><head></head><body><h1>ParticleSystem</h1>
<p>The <code v-pre="">vc-primitive-particle</code> component is used to load a ParticleSystem manages the updating and display of a collection of particles.</p>
<h2>Example</h2>
<h3>Load Particle System Fireworks</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" should-animate="">
        <vc-primitive-particle v-for="(option, index) of options" :key="index" :image="option.image" :start-color="option.startColor" :end-color="option.endColor" :particle-life="option.particleLife" :speed="option.speed" :image-size="option.imageSize" :emission-rate="option.emissionRate" :emitter="option.emitter" :bursts="option.bursts" :lifetime="option.lifetime" :update-callback="option.updateCallback" :model-matrix="option.modelMatrix" :emitter-model-matrix="option.emitterModelMatrix">
        </vc-primitive-particle>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
