        <span class="hljs-variable language_">this</span>.<span class="hljs-property">emitterInitialLocation</span> = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>(<span class="hljs-number">0.0</span>, <span class="hljs-number">0.0</span>, <span class="hljs-number">100.0</span>)
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">minimumExplosionSize</span> = <span class="hljs-number">30.0</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">maximumExplosionSize</span> = <span class="hljs-number">100.0</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">particlePixelSize</span> = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian2</span>(<span class="hljs-number">7.0</span>, <span class="hljs-number">7.0</span>)
