            <span class="hljs-attr">minimumGreen</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">minimumBlue</span>: <span class="hljs-number">0.8</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          },
          {
            <span class="hljs-attr">red</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">green</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">minimumBlue</span>: <span class="hljs-number">0.8</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          },
          {
            <span class="hljs-attr">minimumRed</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">minimumGreen</span>: <span class="hljs-number">0.75</span>,
            <span class="hljs-attr">blue</span>: <span class="hljs-number">0.0</span>,
            <span class="hljs-attr">alpha</span>: <span class="hljs-number">1.0</span>
          }
        ]

        <span class="hljs-keyword">for</span> (<span class="hljs-keyword">var</span> i = <span class="hljs-number">0</span>; i &lt; numberOfFireworks; ++i) {
          <span class="hljs-keyword">var</span> x = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Math</span>.<span class="hljs-title function_">randomBetween</span>(xMin, xMax)
          <span class="hljs-keyword">var</span> y = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Math</span>.<span class="hljs-title function_">randomBetween</span>(yMin, yMax)
          <span class="hljs-keyword">var</span> z = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Math</span>.<span class="hljs-title function_">randomBetween</span>(zMin, zMax)
          <span class="hljs-keyword">var</span> offset = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>(x, y, z)
