        camera.<span class="hljs-title function_">lookUp</span>(angle)
      },
      <span class="hljs-title function_">createFirework</span>(<span class="hljs-params">offset, color, bursts</span>) {
        <span class="hljs-keyword">var</span> emitterModelMatrixScratch = <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Matrix4</span>()
        <span class="hljs-keyword">var</span> position = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Cartesian3</span>.<span class="hljs-title function_">add</span>(<span class="hljs-variable language_">this</span>.<span class="hljs-property">emitterInitialLocation</span>, offset, <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>())
        <span class="hljs-keyword">var</span> emitterModelMatrix = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Matrix4</span>.<span class="hljs-title function_">fromTranslation</span>(position, emitterModelMatrixScratch)
