        <span class="hljs-keyword">var</span> size = <span class="hljs-title class_">Cesium</span>.<span class="hljs-property">Math</span>.<span class="hljs-title function_">randomBetween</span>(<span class="hljs-variable language_">this</span>.<span class="hljs-property">minimumExplosionSize</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">maximumExplosionSize</span>)
