</tr>
<tr>
<td>particleLife</td>
<td>Number</td>
<td><code v-pre="">5.0</code></td>
<td><code v-pre="">optional</code> If set, overrides the minimumParticleLife and maximumParticleLife inputs with this value.</td>
</tr>
<tr>
<td>minimumParticleLife</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> Sets the minimum bound in seconds for the possible duration of a particle's life above which a particle's actual life will be randomly chosen.</td>
</tr>
<tr>
<td>maximumParticleLife</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> Sets the maximum bound in seconds for the possible duration of a particle's life below which a particle's actual life will be randomly chosen.</td>
</tr>
<tr>
<td>mass</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> Sets the minimum and maximum mass of particles in kilograms.</td>
</tr>
<tr>
<td>minimumMass</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> Sets the minimum bound for the mass of a particle in kilograms. A particle's actual mass will be chosen as a random amount above this value.</td>
</tr>
<tr>
<td>maximumMass</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> Sets the maximum mass of particles in kilograms. A particle's actual mass will be chosen as a random amount below this value.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ParticleSystem.html">ParticleSystem</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>complete</td>
<td></td>
<td>Fires an event when the particle system has reached the end of its lifetime.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          particleCanvas: null,
          options: []
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = cesiumInstance
          var scene = viewer.scene
          scene.debugShowFramesPerSecond = true
          Cesium.Math.setRandomNumberSeed(315)
          this.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(108.59777, 40.03883))
          this.emitterInitialLocation = new Cesium.Cartesian3(0.0, 0.0, 100.0)
          this.minimumExplosionSize = 30.0
          this.maximumExplosionSize = 100.0
          this.particlePixelSize = new Cesium.Cartesian2(7.0, 7.0)
          var burstSize = 400.0
          this.lifetime = 10.0
          var numberOfFireworks = 20.0

          var xMin = -100.0
          var xMax = 100.0
          var yMin = -80.0
          var yMax = 100.0
          var zMin = -50.0
          var zMax = 50.0

          var colorOptions = [
            {
              minimumRed: 0.75,
              green: 0.0,
              minimumBlue: 0.8,
              alpha: 1.0
            },
            {
              red: 0.0,
              minimumGreen: 0.75,
              minimumBlue: 0.8,
              alpha: 1.0
            },
            {
              red: 0.0,
              green: 0.0,
              minimumBlue: 0.8,
              alpha: 1.0
            },
            {
              minimumRed: 0.75,
              minimumGreen: 0.75,
              blue: 0.0,
              alpha: 1.0
            }
          ]

          for (var i = 0; i < numberOfFireworks; ++i) {
            var x = Cesium.Math.randomBetween(xMin, xMax)
            var y = Cesium.Math.randomBetween(yMin, yMax)
            var z = Cesium.Math.randomBetween(zMin, zMax)
            var offset = new Cesium.Cartesian3(x, y, z)
            var color = Cesium.Color.fromRandom(colorOptions[i % colorOptions.length])

            var bursts = []
            for (var j = 0; j < 3; ++j) {
              bursts.push(
                new Cesium.ParticleBurst({
                  time: Cesium.Math.nextRandomNumber() * this.lifetime,
                  minimum: burstSize,
                  maximum: burstSize
                })
              )
            }

            this.createFirework(offset, color, bursts)
          }

          var camera = viewer.scene.camera
          var cameraOffset = new Cesium.Cartesian3(-300.0, 0.0, 0.0)
          camera.lookAtTransform(this.modelMatrix, cameraOffset)
          camera.lookAtTransform(Cesium.Matrix4.IDENTITY)

          var toFireworks = Cesium.Cartesian3.subtract(this.emitterInitialLocation, cameraOffset, new Cesium.Cartesian3())
          Cesium.Cartesian3.normalize(toFireworks, toFireworks)
          var angle = Cesium.Math.PI_OVER_TWO - Math.acos(Cesium.Cartesian3.dot(toFireworks, Cesium.Cartesian3.UNIT_Z))
          camera.lookUp(angle)
        },
        createFirework(offset, color, bursts) {
          var emitterModelMatrixScratch = new Cesium.Matrix4()
          var position = Cesium.Cartesian3.add(this.emitterInitialLocation, offset, new Cesium.Cartesian3())
          var emitterModelMatrix = Cesium.Matrix4.fromTranslation(position, emitterModelMatrixScratch)
          var particleToWorld = Cesium.Matrix4.multiply(this.modelMatrix, emitterModelMatrix, new Cesium.Matrix4())
          var worldToParticle = Cesium.Matrix4.inverseTransformation(particleToWorld, particleToWorld)

          var size = Cesium.Math.randomBetween(this.minimumExplosionSize, this.maximumExplosionSize)
          var particlePositionScratch = new Cesium.Cartesian3()
          var force = function(particle) {
            var position = Cesium.Matrix4.multiplyByPoint(worldToParticle, particle.position, particlePositionScratch)
            if (Cesium.Cartesian3.magnitudeSquared(position) >= size * size) {
              Cesium.Cartesian3.clone(Cesium.Cartesian3.ZERO, particle.velocity)
            }
          }

          var normalSize = (size - this.minimumExplosionSize) / (this.maximumExplosionSize - this.minimumExplosionSize)
          var minLife = 0.3
          var maxLife = 1.0
          var life = normalSize * (maxLife - minLife) + minLife
          this.options.push({
            image: this.getImage(),
            startColor: color,
            endColor: color.withAlpha(0.0),
            particleLife: life,
            speed: 100.0,
            imageSize: this.particlePixelSize,
            emissionRate: 0,
            emitter: new Cesium.SphereEmitter(0.1),
            bursts: bursts,
            lifetime: this.lifetime,
            updateCallback: force,
            modelMatrix: this.modelMatrix,
            emitterModelMatrix: emitterModelMatrix
          })
        },
        getImage() {
          let particleCanvas = this.particleCanvas
          if (!Cesium.defined(particleCanvas)) {
            particleCanvas = document.createElement('canvas')
            particleCanvas.width = 20
            particleCanvas.height = 20
            var context2D = particleCanvas.getContext('2d')
            context2D.beginPath()
            context2D.arc(8, 8, 8, 0, Cesium.Math.TWO_PI, true)
            context2D.closePath()
            context2D.fillStyle = 'rgb(255, 255, 255)'
            context2D.fill()
          }
          return particleCanvas
        }
      }
    }
  </script>